.createNote{
    margin: 50px 0 50px 0;
}


.createContainer{
    padding: 15px;
    background-color: rgb(255, 255, 255);
    max-width: 800px;
    margin: auto;
    border: 2px black solid;
}

.createNote input[type="text"], textarea{
    background-color: transparent;
    width: 100%;
    height:5vh;
    border: none;
    border-bottom: 2px solid rgb(98, 98, 98);
}

textarea{
    height: 5vh;
    resize: none;
}


textarea::-webkit-scrollbar { width: 0 !important }
textarea { overflow: -moz-scrollbars-none; }
textarea { -ms-overflow-style: none; }

.createNote input:focus{
    width: 100%;
    outline: none;
}


.createNote .bottom{
    margin-top: 20px;
    display: flex;
}

.createNote .colors{
display: flex;
width: 100%;
margin: 0 10px 0 10px;
}

.createNote .color{
    height:30px;
    width:30px;
    border-radius: 50%;
    margin: 0 10px 0 10px;
    
}

.createNote .active{
    border: 2px solid black;
    color: rgb(49, 49, 49);
    cursor: pointer;
}

.createNote .notActive{
    border: 1px solid rgb(167, 167, 167);
    cursor: pointer;
}


.createNote .exit{
    text-align: center;
    margin: 0;
    color: red;
    cursor: pointer;
    width: 20px;
    opacity: .5;
}
.createNote .exit:hover{
    opacity: 1;
}

.createNote h2{
    margin: 2px 0 5px 0
}

.colorOne{
    background-color: rgb(255, 252, 232);
}
.colorTwo{
    background-color: rgb(133, 182, 133);
}
.colorThree{
    background-color: rgb(234, 170, 190);
}
.colorFour{
    background-color: rgb(171, 132, 255);
}
.colorFive{
    background-color: rgb(131, 207, 255)
}
.colorSix{
    background-color: rgb(255, 255, 255)
}

.createNote input[type="submit"]{
    display: inline-block;
    width: 25%;
    margin-top: 5px;
    color: #334747;
    font-size: 16px;
    letter-spacing: 1px;
    cursor: pointer;
    background: transparent;
    border: 1px solid #334747;
    border-radius: 30px;
    /* Makes the semi-circle */
    box-shadow: inset 0 0 0 0 #334747;
    transition: .3s;
}

.createNote input[type=submit]:hover{
    color: #ffffff;
    box-shadow: inset 250px 0 0 0 #334747;
}

#top{
    height:0
}

.categories{
    display: flex;
    padding-bottom: 7px;
    border-bottom: 2px solid rgb(98, 98, 98);
}

.category{
    margin: 0 10px 0 0px;
    padding:0 3px 0 3px;
    border-radius: 7px;
}

.categories .active{
    background-color: rgba(58, 58, 58, 0.25);
}

.createNote p{
    margin: 10px 0 10px 0px;
}

.createNote img{
    width:100%
}

.edit-note{
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}